:root{
  --dominant: #0E0741;
  --white: white;
  --grey: #d7d7d9;
  --darkGrey: #383838;
  --blue: rgba(0,22,255,1);
  --red: rgba(255,0,0,1);
  --purple: rgba(146,110,220,1);
  --green: #03C03C;
  --Rojo: linear-gradient(180deg, rgba(228,30,43,1) 0%, rgba(255,0,0,1) 100%);
  --Azul: linear-gradient(180deg, rgba(35,25,208,1) 0%, rgba(0,22,255,1) 100%);
  --Morado: linear-gradient(180deg, rgba(209,126,242,1) 0%, rgba(146,110,220,1) 100%);
  --Verde: linear-gradient(180deg, #ADFF2F 0%, #03C03C 100%);
  

}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: 'Poppins', sans-serif;
}

.App {
  text-align: center;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.AppUIdarkAzul{
  background-color: rgb(0, 4, 44);
  min-height: 100vh;
}
.AppUIdarkRojo{
  background-color: var(--darkGrey);
  min-height: 100vh;
}
.AppUIdarkMorado{
  background-color: blueviolet;
  min-height: 100vh;
}
.AppUIdarkVerde{
  background-color: blueviolet;
  min-height: 100vh;
}

header {
  width: 100%;
  min-height: 3vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  padding: 15px 0px;
  transition-duration: 0.4s;
  color: var(--white)

}
.header_Azul{
  background: var(--Azul) 
}
.header_Rojo{
  background: var(--Rojo) 
}
.header_Morado{
  background: var(--Morado) 
}
.header_Verde{
  background: var(--Verde) 
}
.header_Blanco{
  background: var(--grey);
  color: var(--darkGrey)
}

h1 {
  font-size: 16pt;
  font-weight: 300;

}

.closeModals{
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  z-index: 10;
}

.createTodoButton {
    width: 10vh;
    height: 10vh;
    border: none;
    border-radius: 50%;
    position: fixed;
    bottom: 5vh;
    right: 5vw;
    font-size: 1.9rem;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition-duration: 0.4s;
    z-index: 999;
    color: #fff;
}
.createTodoButton:hover{
    transition-duration: 0.4s;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.button_Azul{
    background-color: var(--blue);
}
.button_Rojo{
    background-color: var(--red);
}
.button_Morado{
    background-color: var(--purple);
}
.button_Verde{
    background-color: var(--green);
}
.button_Blanco{
    background-color: var(--darkGrey);
}
.todoInput { 
    background-color: var(--white);
    width: 75%;
    border-radius: 5px;
    border:none;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    margin: 12px 0px;

}
.itemContainer{
    display: flex;
    align-items: center;
    background-color: var(--white);
    padding: 15px 40px;
    border-radius: 0.8vh;
    justify-content: space-between;
    color: var(--grey);
    margin: 5px 0;

}

.textContainer{
    display: flex;
    flex-direction:column;

}
.iconsContainer{
    display: flex;
    flex-direction:row;

}

.titulo{
    margin: 0;
    color: #939099;
    text-transform: capitalize;
}
.titulo--active{
    margin: 0;
    color: var(--grey);
    text-transform: uppercase;
    text-decoration:line-through;
}

.texto{
    margin: 0;
}

.icon{
    width: 26px;
    height: 26px;
    margin:3px;
}

.icon-check{
    position: relative;
    background-image: url(/Todo-List/static/media/check-grey.33c39d44.svg);

}
.icon-check--active{
    background-image: url(/Todo-List/static/media/check-color.6ff24850.svg);
}
.icon-delete{
    position: relative;
    background-image: url(/Todo-List/static/media/trash.d80cebf9.svg);
    transition-duration: 0.4s;
}
.icon-delete:hover{
    position: relative;
    background-image: url(/Todo-List/static/media/trash-red.bb47648a.svg);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);

}
ul{
    list-style: none;
    padding: 0;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90%;
    max-width: 1024px;
}
.formContainer{
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    z-index: 990;

    }
      
.formTitle {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    }
    
.todoTitle{
    background-color: #F9FBFC;
    width: calc(100% - 25px);
    border-radius: 5px;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    margin: 12px 0px;
}

.todoDescript {
    background-color: #F9FBFC;
    border-radius: 5px;
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    resize: none;
}

.inputsAzul{
    border-color: var(--blue);
}
.inputsRojo{
    border-color: var(--red);
}
.inputsMorado{
    border-color: var(--purple);
}
.inputsVerde{
    border-color: var(--green);
}

.todoDescript::-webkit-input-placeholder {
    color: #A5A5A5;
    font-weight: 400;
}

.todoDescript::placeholder {
    color: #A5A5A5;
    font-weight: 400;
}

textarea:focus {
    outline-color: #61DAFA;
    }

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 110px;
    height: 48px;
    border-radius: 2px;
    border: none;
    transition-duration: 0.4s;
}

.addAzul {
    background: var(--blue);
    color: #fff;
    font-weight: 400;
}
.addRojo {
    background: var(--red);
    color: #fff;
    font-weight: 400;
}
.addMorado {
    background: var(--purple);
    color: #fff;
    font-weight: 400;
}
.addVerde {
    background: var(--green);
    color: #fff;
    font-weight: 400;
}
.addBlanco {
    background: var(--darkGrey);
    color: #fff;
    font-weight: 400;
}


.addAzul:hover {
    box-shadow: 0px 5px 25px  var(--blue);    
}
.addRojo:hover {
    box-shadow: 0px 5px 25px  var(--red);
}
.addMorado:hover {
    box-shadow: 0px 5px 25px  var(--purple);
}
.addVerde:hover {
    box-shadow: 0px 5px 25px  var(--green);    
}
.addBlanco:hover {
    box-shadow: 0px 5px 25px  var(--darkGrey);

}

.TodoForm-button--cancel {
background: transparent;
}

.closeButton{
    width: 30px;
    height: 30px;
    padding: 0;
    margin: 0;
    font-size: 20px;
    right: 10px;
    font-weight: bold;
    transition-duration: 0.4s;
}

.closeButton:hover{
    color: var(--red);
}

.closeContainer{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 16px 0;
}

.emptyTodo{
    color: var(--red);
    text-align: center;
    margin:15px 0 0 0;
}
@-webkit-keyframes placeHolderShimmer {
    0% {
      background-position: -800px 0
    }
    100% {
      background-position: 800px 0
    }
  }
  

@keyframes placeHolderShimmer {
    0% {
      background-position: -800px 0
    }
    100% {
      background-position: 800px 0
    }
  }
  

.loadingSkeleton {
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: placeHolderShimmer;
            animation-name: placeHolderShimmer;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    background-color: #f6f7f8;
    background: linear-gradient(to right, rgb(245, 245, 245) 8%, #cacaca 18%, rgb(245, 245, 245) 33%);
    width: 90%;
    height: 70px;
    margin: 12px;
    border-radius: 0.8vh;
  }
.createTodoBackground{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10% 0;
}

.plusSign{
    width: 60%;
    height: 300px;
    color: rgb(216, 216, 216);
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;

}

.addIcon{
    background: url(/Todo-List/static/media/addTodo.faabd1bb.svg);
    width: 150px;
    height: 150px;
    background-repeat: no-repeat;    
}

.addTodoText{
    margin: 0;
}

@media (max-width: 500px) {
    .plusSign{
        font-size: 20px;
        
    
    }    
}
.ThemeButton {
    width: 6vh;
    height: 6vh;
    border: none;
    border-radius: 50%;
    position: fixed;
    bottom: 16vh;
    right: 5vw;
    background-color: var(--grey);
    font-size: 1.9rem;
    z-index: 998;
    display: flex;
    justify-content: center;
    align-items: center;
    transition-duration: 0.4s;
}

.icon-theme{
    background: url(/Todo-List/static/media/brocha.1798e731.svg);
    background-repeat: no-repeat;
    background-position: center;  
    opacity: 50%;
    width: 80%;
    height: 80%;
    transition-duration: 0.4s;

    
}
.icon-theme:hover{
    opacity: 80%;
}
.themeContainer{
    position: fixed;
    position: fixed;
    bottom: 17vh;
    right: 4.5vw;
    max-width: 900px;
    min-width: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    border-radius: 10px;
    z-index: 11;
}

.themeContainer ul{
    margin: 15px 0px 50px;
    display: flex;
}

.themeList{
    width: 5vh;
    height: 5vh;
    border-radius: 50%;
    margin: 3px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition-duration: 0.4s
}

.lightAzul{
    border: solid 2px var(--blue);
    background-color: var(--white);
}
.lightRojo{
    border: solid 2px var(--red);
    background-color: var(--white);

}
.lightMorado{
    border: solid 2px;
    border-color: var(--purple);
    background-color: var(--white);

}
.lightVerde{
    border: solid 2px var(--green); 
    background-color: var(--white);
}

.lightBlanco{
    border: solid 2px rgb(187, 187, 187);
    background-color: var(--white);
}

/*hover*/
.lightAzul:hover{
    background-color: var(--blue);
    color: #fff;

}

.lightRojo:hover{
    background-color: var(--red);
    color: #fff;
}

.lightMorado:hover{
    background-color: var(--purple);
    color: #fff;


}

.lightVerde:hover{
    background-color: var(--green);
    color: #fff;

}

.lightBlanco:hover{
    background-color: rgb(187, 187, 187);
    color: var(--darkGrey);


}

/* dark theme never used */


.darkAzul{
    border: solid 5px var(--blue);
    background-color: #000;
    color: #fff;
}
.darkRojo{
    border: solid 5px var(--red);
    background-color: #000;
    color: #fff;
}
.darkMorado{
    border: solid 5px var(--Morado);
    background-color: #000;
    color: #fff;


}
.darkVerde{
    border: solid 2px green;
    background-color: #000;
    color: #fff;

}
.darkNegro{
    border: solid 2px #000;
    background-color: #000;
    color: #fff;
}
